
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { fetchMsgList } from '@/api/message'
import { bus } from '../../main'
export default {
components: {
XHeader,
Group,
Cell,
Search,
Badge
},
data () {
return {
msgList: [],
unReadList: [],
title: '',
read: 0
}
},
mounted () {
this.getMsgList()
},
created () {
bus.$on('refresh', () => {
this.getMsgList()
})
},
methods: {
getMsgList () {
this.getUnReadMsgList()
this.getReadedMsgList()
bus.$emit('refreshMsgCount')
},
getUnReadMsgList () {
fetchMsgList({read: '0'}).then(response => {
let data = response.data
this.unReadList = data.msglist
})
},
getReadedMsgList () {
fetchMsgList({read: '1'}).then(response => {
let data = response.data
this.msgList = data.msglist
})
},
searchMsgList () {
fetchMsgList({title: this.title}).then(response => {
this.unReadList = []
this.msgList = response.data.msglist
})
},
refresh (done) {
this.getMsgList() 
done()
},
onSubmit () {
this.$refs.msgSearch.setBlur()
this.searchMsgList()
},
onCancel () {
this.getMsgList()
}
}
}



